import React, { FC } from 'react'
import clsx from 'clsx'
import { Typography } from '../../other/Typography'
import { BriefCard } from '../../cards'
import { Condition } from '../../other'
import { VariantButtonProps } from './types'
import './VariantButton.scss'

const VariantButton: FC<VariantButtonProps> = ({
  isAccented = true,
  title = '',
  className,
  isOutOfStock = false,
  imgLqip = 'images/productCardThumbnail.png',
  imgSrc = '',
  imgAlt = '',
  loading = 'lazy',
  outOfStockTitle = '',
  price = 0,
  onClick,
}) => {
  return (
    <button
      className={clsx('VariantButton', className)}
      disabled={isOutOfStock}
      onClick={onClick}
    >
      {' '}
      <Condition match={!isAccented}>
        <BriefCard
          imgAlt={imgAlt}
          imgLqip={imgLqip}
          imgSrc={imgSrc}
          isOutOfStock={isOutOfStock}
          loading={loading}
          outOfStockTitle={outOfStockTitle}
          price={price}
          title={title}
        />
      </Condition>
      <div
        className={clsx(
          'VariantButton__Card',
          className,
          clsx({
            '--accent': isAccented,
          }),
        )}
      >
        <Typography
          className='VariantButton__Card-Title'
          text={title}
          type='Body1B'
        />
      </div>
    </button>
  )
}

export default VariantButton
